body {
  background-color: hsl(212, 45%, 89%);
}

.App {
  margin: 0 auto;
  margin-top: 180px;
}

.MainComponent {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background-color: hsl(0, 0%, 100%);
  margin: 0 auto;
  margin-top: 50px;
  margin-bottom: 50px;
  width: 300px;
  height: 500px;
  border-radius: 25px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.ImageComponent img {
  margin-top: 15px;
  width: 260px;
  height: 260px;
  border-radius: 25px;
}

.TextComponent1 {
  margin-top: 20px;
  margin-right: 20px;
  margin-left: 20px;
  font-weight: bold;
  font-size: 20px;
}

.TextComponent2 {
  color: hsl(220, 15%, 30%);
  margin-right: 20px;
  margin-left: 20px;
  font-size: 15px;
}
